import axios from "axios";
import api from "../common/js/api";

export default {
    //
    getCustomer({commit}){
        axios.get(api.customerList)
            .then(function (res) {
                if (res.data.code === 1) {
                    commit("initCustomer", res.data.data);
                } else {
                    console.log("");
                }
            })
            .catch(err => {
                console.log("");
                console.log(err);
            });
    },

    //
    getGoods({commit}){
        axios.get(api.goodsList)
            .then(res => {
                commit("initGoods", res.data);
            })
            .catch(err => {
                console.log("");
                console.log(err);
            })
    },

    //
    getCategory({commit}){
        axios.get(api.goodsCategory)
            .then(res => {
                commit("initCategory", res.data);
            })
            .catch(err => {
                console.log("");
                console.log(err);
            })
    }
}


